function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: '1',
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        pagination: {
            el: ".main .swiper-pagination",
            clickable: true,
        },
    })
}

function sliderCategory() {
    var swiper = new Swiper('.category-slider .swiper', {
        slidesPerView: 4,
        spaceBetween: 16,
        pagination: {
            el: ".category-slider .swiper-pagination",
            clickable: true,
            dynamicBullets: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
            },
            577: {
                slidesPerView: 3,
            },
            761: {
                slidesPerView: 4,
            }
        }
    })
}

function sliderCategories() {
    var swiper = new Swiper('.categories-main .swiper', {
        slidesPerView: "auto",
        spaceBetween: 16,
    })
}

